function TranscriptView(props) {
  const thiz = props.context;
  var table = [];
  //table.push(<div className="transcript-renderer text-left">);
  const transcripts = props.context.state.transcript;
  var N = transcripts.length;
  
  for (let i = 0; i < N; i=i+1) {
    var transcript = transcripts[i];
    var future_time = thiz.state.transcript[Math.min(N-1,i+1)].time;
    var current_time = transcript.time;
    if(future_time > thiz.state.currentTimeAroll && current_time < thiz.state.currentTimeAroll) {
      table.push(<div className="bg-primary text-white" onClick={props.context.clickTranscript(transcript.time)}><b>{transcript.time_readable}</b> {transcript.text}</div>);
      transcript_position = i;
    } else {
        var found_broll = false;
        for (let i = 0;i < added_broll_position.length && !found_broll; i=i+1) {
          if(added_broll_position[i] > current_time/1000 && added_broll_position[i] < future_time/1000) {
            found_broll = true;
          }
        }
        
        if (found_broll) {
          table.push(<div className="bg-warning" onClick={props.context.clickTranscript(transcript.time)}><b>{transcript.time_readable}</b> {transcript.text}</div>);
        } else {
          table.push(<div className="" onClick={props.context.clickTranscript(transcript.time)}><b>{transcript.time_readable}</b> {transcript.text}</div>);
        }
        
    }
  }
  return table
}

