  callingUpdate = () => (event) => {
    this.setState({currentTimeAroll: event.target.currentTime*1000, currentTimeArollFraction: (event.target.currentTime/event.target.duration).toFixed(2)})
    //document.getElementById("demo").innerHTML = (event.target.currentTime/event.target.duration).toFixed(2)
    for (var i = 0;i < added_broll_position.length; i++) {
      console.log(added_broll_position)
      if(added_broll_position[i] > prev_pos && added_broll_position[i] < event.target.currentTime) {
        console.log("starting broll")
        showThisBroll(i)
        break;
      }
    }
    prev_pos = event.target.currentTime;

    var xaroll = (event.target.currentTime/event.target.duration).toFixed(2) * 800;
    if(xaroll>800){
      xaroll = 800;
    }
    if (!document.getElementById("aroll").paused) {
      var target = document.getElementById("aroll-control");
      target.style.webkitTransform =
      target.style.transform =
        'translate(' + xaroll + 'px, ' + 0 + 'px)';

      // update the posiion attributes
      target.setAttribute('data-x', xaroll);
    }
    $("#transcript-renderer").scrollTop(transcript_position*26.67);
  }

